/* ------------------------------------------------------------ *\
	Base
\* ------------------------------------------------------------ */

body { min-width: 320px; background: #fff; font-family: $font-open-sans; font-size: $font-size-base; line-height: $line-height-base; color: #000; }

a { color: inherit; text-decoration: underline; }
a:hover { text-decoration: none; }

a[href^="tel"] { text-decoration: none; }

h1 { font-size: 42px; font-weight: 600; color: #333; margin-bottom: 0;  }
h2 { font-size: 36px; font-weight: 600; line-height: 1;  color: #333;  }
h3 { font-size: 30px; line-height: 1.07; color: #fff; text-transform: uppercase; }
h4 { font-size: 28px; line-height: 1.21; font-weight: 400; color: #fff; }

h1,
h2,
h3,
h4,
h5,
h6 { margin-bottom: calc($(line-height-base)em/2); }

p,
ul,
ol,
dl,
table,
blockquote { margin-bottom: $(line-height-base)em; }

h1[class],
h2[class],
h3[class],
h4[class],
h5[class],
h6[class],
h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
p:last-child,
ul:last-child,
ol:last-child,
dl:last-child,
table:last-child,
blockquote:last-child { margin-bottom: 0; }
